.full-screen{
    height: 100%;
    min-width: 100%;
    overflow: hidden;
}

.carousel{
    z-index: 1;
}

.self-intro{
    height: 260px;
    width: 100%;
    background: rgb(192,192,192, .5);
}

.hero{
    width: 100%;
    height: 100vh;
    background: url(./../img/jp/image23.JPG) top center;
    background-size: cover;
    position: relative;
    margin-top: -20px;
    z-index: -1;
  }

.content{
    padding: 40px;
}

.black-20p{
    font-size: 20px;
    color:black;
    font-family: 'Lato', sans-serif;
}

.grey-36p{
    font-size: 36px;
    color: grey;
    font-family: 'Montserrat',sans-serif;
}

.hero-container{
    position: absolute;
    bottom: 0;
    top: 150px;
    right: 0;
    display: flex;
    flex-direction: column;
  }